import React, {Component} from "react";
import MultiCartExpandingSectionComponent from "../shared/MultiCartExpandingSectionComponent";
import MultiCartMobileCollapsedItemComponent from "./MultiCartMobileCollapsedItemComponent";
import MultiCartAssignmentInstalmentComponent from "./MultiCartAssignmentInstalmentComponent";
import MultiCartAssignmentDeathComponent from "./MultiCartAssignmentDeathComponent";
import {connect} from "react-redux";
import ModalService from "eshop/flux/utils/OraModalService";
import MultiCartRemovePopup from "./MultiCartRemovePopup"
import {removeFromCart, setFilters, showErrorMessage} from "eshop/modules/cart/actions/cart";
import ServiceEntryComponent from "./productEntries/ServiceEntryComponent";
import TerminalEntriesListComponent from "./productEntries/TerminalEntriesListComponent";
import VasEntriesListComponent from "./productEntries/VasEntriesListComponent";
import VasBuyMoreComponent from "./productModals/VasBuyMoreComponent";
import SelectRetentionBonusComponent from "./productModals/SelectRetentionBonusComponent";
import BuyMoreProductsComponent from "./productModals/BuyMoreProductsComponent";
import {getBaseProductIdForBundle} from "../../../selectors";
import {getMarketContext} from "eshop/modules/configurator/selectors/filters"
import {getMobileVases} from "eshop/modules/cart/selectors";

class MobileComponent extends Component {
    constructor(props) {
        super(props);
        this.addButtonClicked = this.addButtonClicked.bind(this);
    }

    openRemovePopup(entry) {
        if (this.props.sapReservation !== "") {
            this.props.showErrorMessage(this.props.sapErrorMessage);
        } else {
            ModalService.open("remove-from-cart-modal-" + entry.bundleNo, {
                bundleNo: entry.bundleNo,
                offerIndex: entry.bundleNo,
            });
        }
    }

    registerRemovePopup(entry) {
        ModalService.add(data => <MultiCartRemovePopup {...this.props}
            {...this.getRemoveModalTexts("SERVICE")}
            id={"remove-from-cart-modal-" + entry.bundleNo}
            bundleNo={entry.bundleNo}
            onClickRemove={this.props.removeFromCart.bind(this, null, entry.bundleNo)}
            textRepresentation={entry.planName}/>);
    }

    componentDidMount() {
        this.registerRemovePopup(this.props.entry);
        //this.props.fetchMobileVases();
    }

    getRemoveModalTexts(type) {
        switch (type) {
        case "SERVICE":
            return {
                title: this.props.sectionConf.deleteEntryPopupTitle,
                content: this.props.sectionConf.deleteEntryPopupContent,
                decline: this.props.sectionConf.declineDeleteEntryPopupButtonLabel,
                confirm: this.props.sectionConf.confirmDeleteEntryPopupButtonLabel,
            };
        case "TERMINAL":
            return {
                title: this.props.sectionConf.deleteTerminalPopupTitle,
                content: this.props.sectionConf.deleteTerminalPopupContent,
                decline: this.props.sectionConf.declineDeleteTerminalPopupButtonLabel,
                confirm: this.props.sectionConf.confirmDeleteTerminalPopupButtonLabel,
            };
        }
    }

    addButtonClicked() {
        console.log("Add button clicked");
        this.props.setFilters(this.props.entry.bundleNo);
        if (this.props.baseProductId) {
            let returnLink = this.props.isB2B ? "/male-firmy/sklep" : "/sklep";
            window.location = returnLink + "?serviceplan=" + this.props.baseProductId + "&propositionId=" + this.props.entry.bundleCode;
        }
    }

    render() {
        const mainIcon = this.props.sectionConf ? this.props.sectionConf.icon : "sim";
        const title = this.props.sectionConf ? this.props.sectionConf.sectionTitle : "Usługa mobilna";
        const productSections = this.props.sectionConf.productSections;
        const idx = this.props.idx;
        const trigger = "ost-" + this.props.entry.bundleNo;

        return (
            <MultiCartExpandingSectionComponent title={title} key={"expandingSection_" + idx} entry={this.props.entry}
                conf={this.props.sectionConf}
                onRemoveClicked={this.openRemovePopup.bind(this, this.props.entry)}
                trigger={trigger}
                isB2B={this.props.isB2B}>
                <div className="l-full-row">
                    <div className="l-page-row u-small-padding-left u-small-padding-right">
                        <div className="u-padding-top u-padding">
                            {productSections && productSections.map((section, index) => {
                                switch (section.entryType) {
                                case "SERVICE": {
                                    let buyMoreLabel = this.props.entry ? section.buyMoreLabel : null;
                                    let sectionTitle = this.props.entry ? section.sectionTitle : null;
                                    let terminalSection = productSections.find(element => element.entryType === "TERMINAL");
                                    let buyMoreTerminalsLabel = !!terminalSection && (!this.props.entry.terminals || terminalSection.limitForBuyMoreButton) >= this.props.entry.terminals.length ? terminalSection.buyMoreLabel : null; //voiceEntryTerminalSection configuration
                                    let terminalsSectionTitle = !!terminalSection && this.props.entry.terminals && this.props.entry.terminals.length ? terminalSection.sectionTitle : null;
                                    let shallShowTerminalsSection = terminalSection && this.props.entry.terminals && this.props.entry.terminals.length > 0;
                                    let shallShowEuroSetsSection = terminalSection && this.props.entry.euroSets && this.props.entry.euroSets.length > 0;
                                    let shallShowAddTerminalToOffer = (!shallShowTerminalsSection && !shallShowEuroSetsSection) &&  !["ASSIGNMENT","MNP_APPLICATION"].includes(this.props.entry.processType);

                                    return (
                                        <div key={"ServiceWrapper" + index}>
                                            <ProductSectionWrapper
                                                key={"productSectionWrapper_" + index}
                                                entry={this.props.entry}
                                                sectionTitle={sectionTitle}
                                                className="u-box-shadow--s">
                                                <ServiceEntryComponent
                                                    key={this.props.entry.bundleCode + "_bundleNo_" + this.props.entry.bundleNo}
                                                    idx={idx}
                                                    tooltipValues={getTooltipValues(this.props.descriptions)}
                                                    mainIcon={mainIcon}
                                                    entry={this.props.entry}
                                                    detailsHeader={section.detailsLabel}
                                                    onRemoveClicked={this.openRemovePopup.bind(this, this.props.entry)}
                                                    removeModalTexts={this.getRemoveModalTexts(section.entryType)}
                                                    channel={this.props.channel}
                                                    bottomBorder={buyMoreTerminalsLabel ? false : true}
                                                />
                                                {(shallShowTerminalsSection || shallShowEuroSetsSection)
                                                && <TerminalEntriesListComponent
                                                    idx={idx}
                                                    entries={shallShowEuroSetsSection ? this.props.entry.euroSets : this.props.entry.terminals}
                                                    detailsHeader={section.detailsLabel}
                                                    removeModalTexts={this.getRemoveModalTexts(section.entryType)}
                                                    cartBundle={"" + this.props.entry.bundleNo}
                                                    bundle={this.props.entry.productCode}
                                                    propositionId={this.props.entry.bundleCode}
                                                    sapReservation={this.props.sapReservation}
                                                    descriptions={this.props.descriptions}
                                                    lowerInstallmentsActive={this.props.lowerInstallmentsActive}
                                                    sapErrorMessage={this.props.sapErrorMessage}/>}


                                            </ProductSectionWrapper>
                                            {shallShowAddTerminalToOffer &&
                                            <ProductSectionWrapper key={"childProductSectionWrapper_" + idx}>
                                                <BuyMoreProductsComponent
                                                    idx={idx}
                                                    buyMoreLabel={buyMoreTerminalsLabel}
                                                    addButtonClicked={this.addButtonClicked}
                                                    link={this.props.baseProductId}
                                                    icon={terminalSection.icon}
                                                    entry={this.props.entry}/>
                                            </ProductSectionWrapper>}

                                        </div>

                                    );
                                }
                                case "GADGET": {
                                    let buyMoreLabel = !this.props.entry.gadgets || section.limitForBuyMoreButton >= this.props.entry.gadgets.length ? section.buyMoreLabel : null;
                                    let sectionTitle = this.props.entry.gadgets && this.props.entry.gadgets.length ? section.sectionTitle : null;
                                    return (
                                        <ProductSectionWrapper key={"productSectionWrapper_" + index}
                                            className="u-box-shadow u-box-shadow–s u-box-shadow-hover"
                                            entry={this.props.entry}
                                            sectionTitle={sectionTitle}>
                                            {buyMoreLabel &&
                                                <BuyMoreProductsComponent key={"GADGET_" + idx} idx={idx}
                                                    buyMoreLabel={buyMoreLabel}
                                                    icon={section.icon}
                                                    entry={this.props.entry}/>}
                                        </ProductSectionWrapper>
                                    );
                                }
                                case "VAS": {
                                    let buyMoreLabel = !this.props.entry.vases || section.limitForBuyMoreButton >= this.props.entry.vases.length ? section.buyMoreLabel : null;
                                    let sectionTitle = this.props.entry.vases.length && this.hasVasesInServices(this.props.entry.vases,this.props.entry.productCode) ? section.sectionTitle : null;
                                    return (
                                        <div key={"VasWrapper_" + index}>
                                            <ProductSectionWrapper
                                                key={"productSectionWrapper_" + index}
                                                entry={this.props.entry}
                                                sectionTitle={sectionTitle}
                                                className="u-box-shadow--s">
                                                <VasEntriesListComponent
                                                    key={"VAS_" + idx}
                                                    idx={idx}
                                                    entries={this.props.entry.vases}
                                                    detailsHeader={section.detailsLabel}
                                                    cartBundle={"" + this.props.entry.bundleNo}
                                                    bundle={this.props.entry.productCode}
                                                    propositionId={this.props.entry.bundleCode}
                                                />
                                                <MultiCartAssignmentInstalmentComponent {...this.props}/>
                                                <MultiCartAssignmentDeathComponent {...this.props}/>

                                            </ProductSectionWrapper>
                                            {buyMoreLabel &&
                                            <VasBuyMoreComponent
                                                idx={idx} buyMoreLabel={buyMoreLabel}
                                                icon={section.icon} entry={this.props.entry}/>}
                                        </div>
                                    );
                                }
                                case "RETENTIONBENEFITS": {
                                    let buyMoreLabel = !this.props.entry.vases || section.limitForBuyMoreButton >= this.props.entry.vases.length ? section.buyMoreLabel : null;
                                    let sectionTitle = this.props.entry.vases.length ? section.sectionTitle : null;
                                    return (
                                        <SelectRetentionBonusComponent
                                            key={"RetentionBonusCmp_"+index}
                                            idx={idx}
                                            buyMoreLabel={buyMoreLabel}
                                            index={index}
                                            sectionTitle={sectionTitle}
                                            icon={section.icon}
                                            entry={this.props.entry}
                                            cartBundle={"" + this.props.entry.bundleNo}
                                            bundle={this.props.entry.productCode}
                                            propositionId={this.props.entry.bundleCode}/>

                                    );
                                }
                                default: {
                                    console.warn("MobileComponent: Unsupported product section type:", section.entryType);
                                    return null;
                                }
                                }
                            })}
                        </div>
                    </div>
                </div>
                <MultiCartMobileCollapsedItemComponent
                    key={"collapsed_" + idx}
                    icon={mainIcon}
                    entry={this.props.entry}
                    trigger={trigger}/>
            </MultiCartExpandingSectionComponent>
        )
    }
    getVasesForBundle(productCode){
        return this.props.vases && this.props.vases.find(vas => vas.bundle === productCode);
    }

    hasVasesInServices(entryVases, productCode) {
        let vases = this.getVasesForBundle(productCode);
        if (!vases || !vases.services) {
            return false
        }
        let services = vases.services;

        let inServices = [];
        services.forEach(service => {
            entryVases.forEach(vas => {
                if (service.id === vas.productCode)
                    inServices.push(vas)}
            )
        });
        return !!inServices.length;
    }
}

const ProductSectionWrapper = props => {
    let icon = props.icon ? props.icon.split("_").join("-") : null;
    return (
        <div className={"g-white1-bg u-margin-top-l" + (props.className ? " " + props.className : "")}>
            {props.sectionTitle &&
            <h3 className="h3 u-small-hide u-padding-all-l u-no-spacing">{props.sectionTitle}</h3>}
            {props.children}
        </div>
    );
};

const getTooltipValues = desc => ({
    notConvergent: desc["tooltip.priceDiscount"] || "def: Kwota abonamentu uwzględnia rabat za zgodę na e-fakturę i terminowe płatności – 5 zł oraz zgodę marketingową – 5 zł.",
    convergent: desc["tooltip.convergentPriceDiscount"] || "Missing business CMS configuration for 'tooltip.convergentPriceDiscount'.",
    mnpPriceInfo: desc["tooltip.mnpPriceInfo"] || "Missing business CMS configuration for 'tooltip.mnpPriceInfo'.",
    voucherDiscountInfo: desc["tooltip.voucherDiscountInfo"] || "opłata abonamentowa uwzględnia rabat za wykorzystany kupon.",
});

const mapStateToProps = () => {
    const getBaseProductId = getBaseProductIdForBundle();

    return (state, props) => ({
        vases: getMobileVases(state),
        baseProductId: getBaseProductId(state, props.entry.bundleNo),
        isB2B: getMarketContext(state) === "B2B",
    });
};

const mapDispatchToProps = dispatch => ({
    removeFromCart: (data, id) => dispatch(removeFromCart(data, id)),
    showErrorMessage: message => dispatch(showErrorMessage(message)),
    setFilters: bundleNo => dispatch(setFilters(bundleNo)),
});

export default connect(mapStateToProps, mapDispatchToProps)(MobileComponent);